<template>
  <div class="register_body">
    <div class="register_box">
      <!--      注册表单区域-->
      <el-form label-width="0px" class="register_form" :model="registerForm" :rules="registerFormRules"
               ref="registerFormRef">
        <!--        账号-->
        <el-form-item prop="username">
          <el-input placeholder="请输入用户名" v-model="registerForm.username" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <!--        密码-->
        <el-form-item prop="password">
          <el-input placeholder="请输入密码" v-model="registerForm.password" prefix-icon="el-icon-lock"
                    show-password></el-input>
        </el-form-item>
        <!--        昵称-->
        <el-form-item prop="nickname">
          <el-input placeholder="请输入用户昵称" v-model="registerForm.nickname"
                    prefix-icon="el-icon-chat-dot-square"></el-input>
        </el-form-item>
        <!--        按钮区域-->
        <el-form-item>
          <el-button class="register_button" @click="Register">注册</el-button>
          <div id="register">
            <div @click="toLogin">返回登录</div>
          </div>
        </el-form-item>
        <!--        其他登录方式-->
        <el-card shadow="never">
          <el-divider><span style="color: #8c8c8c">其他方式登录</span></el-divider>
          <a href="/"><img src="@/assets/img/login/wechat.png"></a>
          <a href="/"><img src="@/assets/img/login/weibo.png" style="margin: 0 65px 0 65px"></a>
          <a href="/"><img src="@/assets/img/login/qq.png"></a>
        </el-card>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      // 登录表单的数据绑定对象
      registerForm: {
        username: '',
        password: '',
        nickname: '',
        role: 2
      },
      // 表单的验证规则对象
      registerFormRules: {
        username: [
          {
            required: true,
            message: '请输入账号',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ], // 用户名是否合法
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur'
          }
        ] // 密码是否合法
      }
    }
  },
  created() {
  },
  methods: {
    // 表单预验证
    Register() {
      this.$refs.registerFormRef.validate(valid => {
        if (!valid) return
        this.$http.post('user', this.registerForm).then(res => {
          this.$message.success('添加用户成功')
          //清空添加用户对话框
          this.$refs.registerFormRef.resetFields()
          this.toLogin()
        }).catch(err => {
          console.log(err);
          this.$message.error('添加用户失败')
        })
    }
)
},
toLogin()
{
  this.$router.push('/login')
}
}
}
</script>

